import { LitElement, html, css } from 'lit';
import { customElement, property, } from 'lit/decorators.js'
import './members_and_connections/view_connections';


@customElement("pdb-connections-connections")
export class PdbConnectionsConnections extends LitElement {
  constructor() { super(); }

  connectedCallback() {
    super.connectedCallback();
  }

  disconnectedCallback() { }
  static styles = [
    css`
   :host { display: block; }
  `
  ];

  render() {

    return html`
      <div class="flex flex-col">
        <pdb-connections-connections-view class="my-1"></pdb-connections-connections-view>
      </div>
    `;
  }

  firstUpdated() { }

  createRenderRoot() {
    return this;
  }
}
